.item-added-to,
.item-added-to-cart-parent {
  position: relative;
  display: flex;
  align-items: center;
}
.item-added-to {
  justify-content: center;
  width: 202px;
  height: 22px;
  flex-shrink: 0;
}
.item-added-to-cart-parent {
  background-color: var(--color-gray-100);
  width: 100%;
  height: 45px;
  overflow: hidden;
  flex-direction: column;
  padding: 11px 0;
  box-sizing: border-box;
  justify-content: flex-end;
  text-align: center;
  font-size: 22px;
  color: var(--color-white);
  font-family: var(--font-langar);
}
